@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@500&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-poppins: Poppins;
  --font-inter: Inter;

  /* font sizes */
  --font-size-mini: 15px;
  --font-size-11xl: 30px;
  --font-size-36xl: 55px;
  --font-size-base: 16px;
  --font-size-sm: 14px;

  /* Colors */
  --white: #fff;
  --color-slateblue: #5b56bd;
  --color-yellow: #e0ff22;
  --primary-black-1: #1e1e1e;

  /* Gaps */
  --gap-10xs: 3px;

  /* Paddings */
  --padding-5xs: 8px;
  --padding-base: 16px;

  /* Border radiuses */
  --br-81xl: 100px;
  --br-31xl: 50px;
  --br-9xs: 4px;
  --br-3xs: 10px;
}
